<template>
    <section class="RecommendationField">
        <h2>おすすめ登録</h2>
        <table v-if="itemList">
            <thead>
                <th>商品名</th>
                <th>商品画像</th>
                <th>カテゴリ</th>
                <th>おすすめ可否</th>
            </thead>
            <tbody v-for="item in itemList" :key="item.id">
                <td>{{ item.name }}</td>
                <td><img :src="'/' + item.image" :alt="item.name" class="item-image"/></td>
                <td><div class="category-box" :style="'background:' + item.color + ';'">{{ item.category }}</div></td>
                <td>
                    <button v-if="!item.isRecommend" class="RecommendBtn" @click="toggleRecommend(item)">おすすめ登録</button>
                    <button v-else class="RecommendBtn cancel" @click="toggleRecommend(item)">おすすめ解除</button>
                </td>
            </tbody>
        </table>
    </section>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    data() {
        return {
            itemList:[]
        }
    },
    mounted() {
        this.getItems();
    },

    methods: {
        getItems() {
            Ajax('/api/kitchen/items')
            .then(res => res.json())
            .then(dat => this.itemList = dat);
        },
        
        toggleRecommend(item) {
            Ajax(`/api/kitchen/items/${item.id}`, 'PUT', item)
            .then(res => {
                if(res.status === 200) {
                    this.getItems();
                }
            });
        }
    }
}
</script>
